<template>
  <div>
    <el-form
      label-position="top"
      label-width="100px"
      :model="inputSupportForm"
      ref="inputSupportForm"
    >
      <div v-for="(year, index) in inputSupportForm.inputSupport" :key="index">
        <el-button
          v-if="index > 0"
          icon="el-icon-delete"
          type="text"
          style="color:red; margin-top: 30px;"
          size="mini"
          @click="removeInputSupport(year._id, index)"
          >Delete Input Support Year</el-button
        >
        <el-row :gutter="20" v-for="(support, idx) in year.inputs" :key="idx">
          <el-col :span="8">
            <el-form-item label="Input Type">
              <el-select
                v-model="support.type"
                style="width:100%; margin-top:-12px"
              >
                <el-option
                  v-for="type in inputTypes"
                  :key="type"
                  :label="type.value"
                  :value="type.value"
                >
                  <span style="float: left">{{ type.value }}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{
                    type.label
                  }}</span>
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="Input Name">
              <el-input v-model="support.name" />
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="Unit Price">
              <el-input
                v-model="support.unit_price"
                type="number"
                @input="setTotal(support, year)"
              />
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <div
              class="button-add-details"
              v-if="idx < 1"
              @click="addDuplicateField(year.inputs, 'input')"
            >
              <i
                class="el-icon-circle-plus"
                style="color: grey; font-size: 20px;"
              ></i>
              <br />
              <el-button type="text" size="mini">Add Input</el-button>
            </div>
            <div
              class="button-add-details"
              v-else
              @click="year.inputs.splice(idx, 1)"
            >
              <i
                class="el-icon-delete"
                style="color: red; font-size: 20px;"
              ></i>
              <br />
              <el-button type="text" size="mini">Remove</el-button>
            </div>
          </el-col>
          <el-col :span="5">
            <el-form-item label="Quantity">
              <el-input
                v-model="support.quantity"
                type="number"
                @input="setTotal(support, year)"
              />
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="Total Amount">
              <el-input v-model="support.total" type="number" disabled>
                <template slot="prepend">GH₵</template>
              </el-input>
            </el-form-item>
          </el-col>
          <hr />
        </el-row>
        <el-row :gutter="20">
          <el-col :span="10">
            <el-form-item label="Year">
              <el-input v-model="year.year" placeholder="2019" />
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="Grand Total">
              <el-input v-model="year.grand_total" type="number" disabled>
                <template slot="prepend">GH₵</template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <hr />
      </div>
      <br />
      <div>
        <el-button
          icon="el-icon-plus"
          type="info"
          class="full-width"
          @click="addInputSupportField"
          >Add Year</el-button
        >
      </div>
      <br /><br />
      <h5>
        Comment: Grand total is the sum of the total amount for each input
        entered.
      </h5>
      <div class="mt-3 d-flex">
        <el-button
          class="full-width"
          type="primary"
          :loading="btnLoading"
          @click="loopOverSupports"
          >Save Details</el-button
        >
      </div>
    </el-form>
  </div>
</template>

<script>
import supportService from '../api/inputSupports';

export default {
  name: 'AddInputSupport',
  props: {
    selectedName: String,
    selectedId: String,
    selectedInputs: {
      type: Array,
      default: function() {
        return [
          {
            farmer: '',
            year: '',
            grand_total: 0,
            inputs: [
              { type: '', name: '', unit_price: 0, quantity: 1, total: 0 },
            ],
          },
        ];
      },
    },
  },
  data() {
    return {
      btnLoading: false,
      inputSupportForm: {
        inputSupport: [
          {
            _id: '',
            farmer: '',
            year: '2020',
            grand_total: 0,
            inputs: [
              {
                type: '',
                name: '',
                unit_price: 0,
                quantity: 1,
                total: 0,
              },
            ],
          },
        ],
      },
      inputTypes: [
        {
          value: 'Seedling',
          label: 'Organic Input & Agrochemical (Fertilizers)',
        },
        {
          value: 'Farm Technology',
          label:
            'Crop Insurance, Crop disease diagnotics tools, Irrigation tech',
        },
        {
          value: 'Farm Implements',
          label: 'Tractor and other farm machinery',
        },
        {
          value: 'Farmer Capacity Training',
          label: 'Farmer Capacity Training',
        },
      ],
    };
  },
  created() {
    this.inputSupportForm.inputSupport = this.selectedInputs;
    console.log(this.inputSupportForm.inputSupport);
  },
  methods: {
    addInputSupportField() {
      let s = {
        farmer: this.selectedId,
        year: '2020',
        grand_total: 0,
        inputs: [
          {
            type: 'Seedling',
            name: 'None',
            unit_price: 0,
            quantity: 1,
            total: 0,
          },
        ],
      };
      supportService
        .addInputSupport(s)
        .then((response) => {
          s = response.data;
          console.log(s);
          this.infoMessage('Added another year');
          this.inputSupportForm.inputSupport.push(s);
        })
        .catch((errors) => this.errorMessage(errors.error));
    },
    setTotal(input, yrs) {
      input.total = input.unit_price * input.quantity;
      const grandTotal = yrs.inputs.reduce(
        (a, b) => ({ total: a.total + b.total }),
        { total: 0 }
      ).total;
      yrs.grand_total = grandTotal;
    },
    loopOverSupports() {
      this.inputSupportForm.inputSupport.forEach((support) =>
        this.updateInputSupport(support)
      );
    },
    updateInputSupport(support) {
      this.btnLoading = true;
      supportService
        .updateSupport(support)
        .then(() => {
          this.addActivity(
            { _id: this.selectedId, name: this.selectedName },
            'Support Updated'
          );
          this.btnLoading = false;
          this.$emit('addedInput');
        })
        .catch((errors) => this.errorMessage(errors.error));
    },
    removeInputSupport(id, index) {
      this.inputSupportForm.inputSupport.splice(index, 1);
      supportService.deleteSupport(id).then(() => {
        this.addActivity(
          { _id: this.selectedId, name: this.selectedName },
          'Support Deleted'
        );
        this.successNotification('Success', 'Support deleted');
      });
    },
  },
};
</script>
